/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

// IE
input[type="search"]::-ms-clear {
	display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

.eltdf-menu-area {

	a {
		color: $first-main-color;
	}
}

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }

		.eltdf-widget-title {
			text-transform: uppercase;
		}
	}
}

.eltdf-side-menu {

	.eltdf-widget-title {
		color: $first-main-color;
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .eltdf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */
$additional-text-color: #999;

.widget {
	margin: 0 0 35px;
	
	.eltdf-widget-title {
        margin: 0 0 17px;
        
        a {
            color: inherit;
        }
    }
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        ul {

            li {

                &.eltdf-bli {
                    margin: 0 0 4px;
                }
            }
        }

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        > h4 {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include eltdfRelativeHolderLayout();
            clear: both;

            > div {
                @include eltdfRelativeHolderLayout();
                border-width: 1px;
                border-style: solid;
                border-color: $first-main-color;
                box-sizing: border-box;
            }
        }

        .screen-reader-text {
            display: none;
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 400;
            font-size: 15px;
            line-height: 52px;
            color: $first-main-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            -webkit-border-radius:0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out);
        }

        input {
			font-family: $default-heading-font;
			font-size: 14px;
			text-transform: uppercase;
            width: calc(100% - 40px);
            padding: 0 0 0 21px;
            border-radius:100px 0 0 100px;
            -webkit-border-radius:100px 0 0 100px;
	
	        @include eltdfPlaceholder {
		        color: $first-main-color;
	        }
        }

        button {
            width: 40px;
            padding: 0 30px 0 9px;
            font-size: 13px;
            cursor: pointer;
            color: $first-main-color;
            @include eltdfTransition(color .2s ease-in-out);

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 1px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }

		a {
			font-family: $default-heading-font;
			font-size: 16px;
			color: $default-text-color;

			&:hover {
				color: $first-main-color;
			}
		}
    }

    &.widget_recent_entries {

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
                    font-family: inherit;
                    font-weight: 400;
                    font-size: 13px;
                    font-style: italic;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        .screen-reader-text {
            display: block;
            margin: 0 0 8px;
            padding: 4px 0 0;
        }

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
			font-size: 18px !important;
			color: $default-text-color;
            background-color: transparent;

            &:after {
                content: ',';
                color: $default-text-color;
                margin: 0;
            }

            &:last-child {

                &:after {
                    display: none;
                }
            }

            &:hover {
                color: $first-main-color;
            }
        }
    }
    
    &.eltdf-blog-list-widget {
        margin: 0 0 35px;
    
        .eltdf-widget-title {
			margin: 0 0 28px;
        }

		p {

			&.eltdf-post-title {
				font-family: $default-heading-font;
				color: $default-heading-color;
				font-size:  18px;
			}

		}

		ul {

			li {

				&:last-child {
					margin: 0;
				}

				.eltdf-bli-content {
					padding: 7px 0 0 20px;
				}

				.eltdf-post-image {
					width: 85px;

					a {
						border-radius: 100%;
						overflow: hidden;

						img {
							display: block;
							width: auto;
							height: 85px;
						}
					}

				}

				.eltdf-post-info-date {

					a {
						font-size: 14px;
						color: $first-main-color;
					}
				}
			}
		}
    }

    &.eltdf-image-widget {
        margin: 0 0 5px;

        .eltdf-widget-title {
            margin: 0 0 35px;
        }

        img {
			display: inline-block;
        }

        .eltdf-drop-down .second .inner & {

            a {
                padding-top: 0;
            }
        }
    }
    
    &.eltdf-image-slider-widget {
        @include eltdfRelativeHolderLayout();
    
        .eltdf-is-widget-inner {
            @include eltdfRelativeHolderLayout();
        }
    
        .eltdf-widget-title {
            margin: 0 0 35px;
        }
    
        img {
            display: block;
        }
    }
	
	&.widget_eltdf_instagram_widget {
		
		.eltdf-widget-title {
			margin: 0 0 28px;
		}
	}
    
    &.eltdf-raw-html-widget {
        margin: 0;
    
        &.eltdf-grid-auto-width {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: auto;
        }
    }

    &.eltdf-separator-widget {
        margin: 0 !important;
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.eltdf-sidebar {

	.widget_nav_menu {

		ul li {
			border-bottom: 1px solid $default-border-color;
		}

		ul li:last-child {
			border-bottom: 0 !important;
		}

		ul.sub-menu {
			border-top: 1px solid $default-border-color;
		}

		ul.menu li a {
			padding-top: 10px;
			padding-bottom: 10px;
			display: block;

			&:hover {
				color: $first-main-color;
			}
		}

		ul.sub-menu {

			> li {

				&:first-child {
					border-top: 0;
				}
			}
		}

		.current-menu-item > a {
			color: $first-main-color;
		}

		.menu-item-has-children > a {
			position: relative;

			&:after {
				content: '\35';
				position: absolute;
				right: 0;
				font-family: 'ElegantIcons';
				font-size: 12px;
				color: #cacaca;
				@include eltdfTransition(transform 0.2s ease-out);
			}

			&.eltdf-custom-menu-active {
				&:after {
					@include eltdfTransform(rotate(90deg));
				}
			}
		}

		li:last-child {
			a {
				padding-bottom: 10px !important;
			}
		}

		.sub-menu {
			display: none;
		}

		.current-menu-ancestor > .sub-menu,
		.current-menu-parent > .sub-menu {
			display: block;
		}
	}
}

.eltdf-page-footer,
.eltdf-top-bar {

    .widget {
		color: #d4d4d4;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
			font-family: $default-heading-font-light;
            margin: 0;
	        color: #fff;
        }
	
	    a {
		    color: $first-main-color;
		
		    &:hover {
			    color: #fff;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: $default-text-color;
        }

        table tbody th {
            border-right-color: $default-text-color;
        }

        /*
        *   Specific styles
        */
        &.widget_text,
		div {
	        color: #d4d4d4;
        }
        
        &.widget_search {

            form {

                > div {
                    border-color: $additional-text-color;
                }
            }

            input {
                color: $additional-text-color;
	            
	            @include eltdfPlaceholder {
		            color: $additional-text-color;
	            }

                &[type='submit'] {
                    color: $additional-text-color;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_comments,
        &.widget_nav_menu {
	
	        .eltdf-footer-widget-title {
                margin: 0 0 13px;
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

			a {
				color: #fff;
			}

            select {
                color: #fff;
                border-color: $additional-text-color;

                option {
                    background-color: $default-heading-color;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #fff;

                &:after {
                    color: $additional-text-color;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        #wp-calendar {

            caption {
                color: #fff;
            }
        }

        &.widget_rss {

            .eltdf-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

		&.widget_eltdf_twitter_widget {

			.eltdf-twitter-widget {

				&.eltdf-twitter-standard{

					li {

						.eltdf-tweet-text {

							span {
								color: #fff;
							}

							a {
								color: $first-main-color;

								&:hover {
									color: #fff
								}
							}
						}
					}
				}
			}
		}

		.owl-dot span,
		.owl-nav div span {
			color: #fff;
			border-color: #fff;
		}

		.owl-dot.active span,
		.owl-dot:hover span {
			background-color: #fff;
			border-color: #fff;
		}
    }
}

.eltdf-page-footer {

	.widget {
		margin: 0 0 20px;
	}
}

.eltdf-footer-bottom-holder {
    
    .widget {
        margin: 0;
    }
}

.eltdf-top-bar {

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

@include laptop-landscape-medium {

	.widget.woocommerce.widget_price_filter {
		padding: 0 0 50px;

		.price_slider_amount .button {
			position: absolute;
			bottom: 0;
		}
	}
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */