@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
	        text-transform: uppercase;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .eltdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

		ul, ol {

			li {
				display: list-item;
			}
		}

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .eltdf-comment-form-inner {
        padding: 45px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
	    text-transform: uppercase;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 16px 24px 15px;
    font-family: inherit;
    font-size: 14px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
	cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }
    
    @include eltdfPlaceholder {
        color: $default-text-color;
    }
}

#respond textarea,
#respond input[type='text'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

#respond textarea {
    display: block;
    padding: 15px 24px;
}

p.form-submit {
    margin: 0;
}

#submit_comment,
.post-password-form input[type='submit'] {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    @include eltdfButtonSolidColor();
	cursor: pointer;
	
    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

%cf7-input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 15px 24px;
    font-family: inherit;
	font-size: 18px;
    line-height: 21px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;

    &:focus {
        color: $default-heading-color;
        background-color: #fff;
    }
	
	@include eltdfPlaceholder {
		color: $default-heading-color;
	}
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %cf7-input-style;

        footer & {
            margin: 0;
        }
    }
}

input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSmallParams();
    @include eltdfButtonSolidColor();
	cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

.wpcf7-form p {
    margin: 0;
}

.wpcf7-form textarea {
    vertical-align: middle;
}

.eltdf-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.wpcf7 {

	p span.ajax-loader {
		display: block;
	}
}

@include ipad-landscape {

	input.wpcf7-form-control.wpcf7-submit {
		border-radius: 0 !important;
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	
    .owl-stage-outer {
        z-index: 1;
    }
    
    &.eltdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			width: 55px;
			height: 55px;
			top: 50%;
			z-index: 3;
			@include eltdfTransform(translateY(-50%));

			.eltdf-nav-arrow {
				position:relative;
				height:55px;
				width:55px;

				.eltdf-top-part,
				.eltdf-bottom-part {
					position:absolute;
					left:0;
					width:40px;
					height:1px;
					background:#000;
					top:50%;
					transform-origin:0 50%;
					@include eltdfTransition(all 0.2s ease-in-out);
				}
			}
			
			&:hover {

				.eltdf-top-part,
				.eltdf-bottom-part {
					background-color: $first-main-color;


				}
			}
		}

		.owl-prev {
			left: 30px;

			.eltdf-top-part,
			.eltdf-bottom-part  {

			}

			.eltdf-top-part {
				@include eltdfTransform(rotate(-45deg));
			}

			.eltdf-bottom-part {
				@include eltdfTransform(rotate(45deg));
			}
		}

		.owl-next {
			right: 30px;

			.eltdf-top-part,
			.eltdf-bottom-part {
				transform-origin:100% 50% !important;
			}

			.eltdf-top-part {
				@include eltdfTransform(rotate(45deg));
			}

			.eltdf-bottom-part {
				@include eltdfTransform(rotate(-45deg));
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				@include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
				box-sizing: border-box;
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.eltdf-404-page {

    &.eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
        height: 100vh;
	    
	    .admin-bar & {
		    height: calc(100vh - 32px);
	    }
    }

    .eltdf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .eltdf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
        text-align: center;

        .eltdf-page-not-found-title {
			font-size: 150px;
			line-height: 1.1;
			margin: 0;
	
	        @include laptop-landscape-medium {
		        font-size: 120px;
	        }
	
	        @include ipad-portrait {
		        font-size: 90px;
	        }
        }
	
	    .eltdf-page-not-found-subtitle {
			position: relative;
			font-size: 100px;
			line-height: 1.15;
			padding: 0 0 50px;
            margin: 0;

			&:after {
				content: " ";
				position: absolute;
				display: block;
				width: 149px;
				left: 50%;
				bottom: 0;
				border-top: 1px solid $first-main-color;
				@include eltdfTransform(translate(-50%));
			}
		
		    @include laptop-landscape-medium {
			    font-size: 70px;
		    }
		
		    @include ipad-portrait {
			    font-size: 40px;
		    }
        }
	    
	    .eltdf-page-not-found-text {
		    margin: 30px 0 0;
	    }

        .eltdf-btn {
			margin: 3.5% 0 0;
			background-color: $first-main-color;
	        
	        @include laptop-landscape-medium {
		        margin: 50px 0 0;
	        }

			&.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg) {

				&:hover {
					border-color: $default-heading-color !important; /* important to override default inline style for buttons */
					background-color: $default-heading-color !important; /* important to override default inline style for buttons */
				}
			}
        }
	
	    .searchform {
		    position: relative;
		    display: inline-block;
		    vertical-align: top;
		    min-width: 400px;
		    margin: 40px 0 0;
		
		    @include phone-landscape {
			    min-width: 240px;
		    }
		
		    input,
		    button {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    float: left;
			    height: 52px;
			    font-family: inherit;
			    font-weight: 400;
			    font-size: 15px;
			    line-height: 52px;
			    color: $first-main-color;
			    background-color: #fff;
			    border: 1px solid $first-main-color;
			    border-radius: 0;
			    -webkit-border-radius:0;
			    outline: 0;
			    -webkit-appearance: none;
			    box-sizing: border-box;
			    @include eltdfTransition(color .2s ease-out);
		    }
		
		    input {
			    font-family: $default-heading-font;
			    font-size: 14px;
			    text-transform: uppercase;
			    width: calc(100% - 52px);
			    padding: 0 0 0 21px;
			    border-right: 0;
			
			    @include eltdfPlaceholder {
				    color: inherit;
			    }
		    }
		
		    button {
			    width: 52px;
			    text-align: center;
			    border-left: 0;
			    padding: 0;
			    font-size: 13px;
			    cursor: pointer;
			    color: $first-main-color;
			    @include eltdfTransition(color .2s ease-in-out);
			
			    span {
				    display: block;
				    line-height: inherit;
				
				    &:before {
					    display: block;
					    line-height: inherit;
				    }
			    }
		    }
	    }
    }

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 40px 30px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 51px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease);
    }

    .eltdf-left-part,
    .eltdf-right-part {
        position:absolute;
        top:23px;
        width:18px;
        height:1px;
        background: $first-main-color;
        @include eltdfTransition(all 0.3s ease);
    }

    .eltdf-left-part {
        left:10px;
        @include eltdfTransform(rotate(-45deg));
    }

    .eltdf-right-part {
        right:10px;
        @include eltdfTransform(rotate(45deg));
    }

    > span {
        position: relative;
        display: block;
        width: 51px;
        height: 51px;
        line-height: 40px;
        font-size: 30px;
        text-align: center;
        color: $first-main-color;
		background-color: #060606;
		border-radius: 50%;
		overflow: hidden;
		box-sizing: border-box;
        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
			vertical-align: middle;
        }

        &:hover {
            background-color: $first-main-color;
			color: #fff;

            .eltdf-left-part,
            .eltdf-right-part {
                background:#fff;
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
