/* ==========================================================================
   Blog List shortcode Common style - begin
   ========================================================================== */

.eltdf-blog-list-holder {
    @include eltdfRelativeHolderLayout();
    clear: both;

    .eltdf-blog-list {
        @include eltdfRelativeHolderLayout();
        list-style: none;
        margin: 0;
        padding: 0;
    }
	
	.eltdf-bl-item {
		@include eltdfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.eltdf-bli-inner {
		@include eltdfRelativeHolderLayout();
	}
	
	.eltdf-bli-content {
		@include eltdfRelativeHolderLayout();
		box-sizing: border-box;
	}
	
	.eltdf-post-image {
		@include eltdfRelativeHolderLayout();
		
		a, img {
			display: block;
		}
	}
	
	.eltdf-bli-excerpt {
		@include eltdfRelativeHolderLayout();
		margin: 0 0 10px;

        .eltdf-post-excerpt {
            &:after {
                content: '...';
            }
        }
	}

	.eltdf-bli-info {
		padding: 0;
		margin: 0 0 10px;

		> div {
			position: relative;
			display: inline-block;
			padding: 0 11px 0 0;
			margin: 0 0 0 5px;
			color: #b2b2b2;

			a {
				color: #b2b2b2;

				&:hover {
					color: $first-main-color;
				}
			}

			&:first-child {
				margin: 0;
			}

			&:after {
				content: "/";
				position: absolute;
				top: 0;
				right: -2px;
			}

			&:last-child {
				padding: 0;
				margin: 0;

				&:after {
					content: '';
				}
			}

			span {
				color: #ababab;
			}
		}

		.eltdf-post-info-author {
			display: inline-block;

			a {
				text-transform: capitalize;

				&:hover {
					color: $first-main-color;
				}
			}
		}

		.eltdf-post-info-comments-holder {
			display: inline-block;

			a {
				display: inline-block;

				span,
				i {
					vertical-align: middle;
					color: #444;
				}
			}
		}

		.eltdf-blog-like {

			i {
				margin: 0 3px 0 0;
			}
		}
	}

    .eltdf-blog-list-button {
        @include eltdfRelativeHolderLayout();
        margin: 10px 0 0;
    }
	
	/***** Columns Space style - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.eltdf-bl-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space, $i+1);
			$column_width_margin: $column_width * 2;
			
			.eltdf-bl-wrapper {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
			}
			
			.eltdf-bl-item {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
	
	/***** Columns Space style - end *****/
}
/* ==========================================================================
   Blog List shortcode Common style - end
   ========================================================================== */