/* ==========================================================================
   Blog Common style - begin
   ========================================================================== */

.eltdf-blog-holder {
    @include eltdfRelativeHolderLayout();
    clear: both;

    article {
        @include eltdfRelativeHolderLayout();
        box-sizing: border-box;

        &.sticky {

            .eltdf-post-title a {
                color: $first-main-color;
            }
        }

        .eltdf-post-content {
            @include eltdfRelativeHolderLayout();
        }

        .eltdf-post-image {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            max-width: 100%;

            img {
                display: block;
            }
        }

		.eltdf-post-title {
			position: relative;
			margin: 0 0 10px;
			padding: 0 0 24px;

			&:before {
				content: " ";
				position: absolute;
				display: block;
				width: 93px;
				bottom: 0;
				left: 0;
				border-top: 1px solid $first-main-color;
			}
		}

        .eltdf-post-text {
            @include eltdfRelativeHolderLayout();
            box-sizing: border-box;
        }

		.eltdf-post-excerpt-holder {
			@include eltdfRelativeHolderLayout();
			margin: 0 0 10px;

			.eltdf-post-excerpt {

				&:after {
					content: '...';
				}
			}
		}

		.eltdf-post-info-bottom {
			padding: 0;
			margin: 0 0 30px;

			> div {
				position: relative;
				display: inline-block;
				padding: 0 11px 0 0;
				margin: 0 0 0 5px;
				color: #b2b2b2;

				a {
					color: #b2b2b2;

					&:hover {
						color: $first-main-color;
					}
				}

				&:first-child {
					margin: 0;
				}

				&:after {
					content: "/";
					position: absolute;
					top: 0;
					right: -2px;
				}

				&:last-child {
					padding: 0;
					margin: 0;

					&:after {
						content: '';
					}
				}
			}

			.eltdf-post-info-author {
				display: inline-block;

				a {
					text-transform: capitalize;

					&:hover {
						color: $first-main-color;
					}
				}
			}

			.eltdf-post-info-comments-holder {
				display: inline-block;

				a {
					display: inline-block;

					span,
					i {
						vertical-align: middle;
						color: #444;
					}
				}
			}

			.eltdf-blog-like {

				i {
					margin: 0 3px 0 0;
				}
			}
		}

		//Quote post style
		&.format-quote {
			background-color: #fafafa;

			.eltdf-post-text {
				padding: 70px;
			}

			.eltdf-post-text-main {
				@include eltdfTableLayout();
				text-align: center;

				a {
					color: $default-text-color;

					&:hover {
						color: $first-main-color;
					}
				}
			}

			.eltdf-post-title {
				padding: 0 0 16px;
				margin: 0;

				&:before {
					display: none;
				}
			}

			.eltdf-quote-author,
			.eltdf-quote-author-position {
				position: relative;
				display: inline-block;
				font-family: $default-heading-font;
				font-size: 23px;
				line-height: 20px;
			}

			.eltdf-quote-author-position {
				padding: 0 0 0 8px;

				&:before {
					content: "/";
					position: absolute;
					top: 0;
					left: -2px;
				}
			}
		}

		//Link post style
		&.format-link {
			background-color: $first-main-color;

			.eltdf-post-text{
				padding: 64px 53px 2px 53px;

				a {
					color: #fff;
				}
			}

			.eltdf-post-text-main {
				@include eltdfTableLayout();
				text-align: left;
			}

			p {

				&.eltdf-post-title {
					text-align: center;
					padding: 0 0 16px;
					margin: 0;

					a {
						font-size: 20px;
						font-family: $default-heading-font;
						text-transform: uppercase;
					}

					&:before {
						display: none;
					}
				}

			}

			.eltdf-link-text {
				display: block;
				font-family: Cambria,Georgia,serif;
				font-size: 18px;
				color: #fff;
				padding: 0 53px 46px;
				margin: 13px 0 0;
				word-wrap: break-word;
			}
		}

        //Gallery post style
        &.format-gallery {

            .eltdf-post-image {
                width: 100%;
            }

            .owl-carousel .owl-item {
                z-index: 2;

                &.active {
                    z-index: 3;
                }
            }
        }

        //Audio post style
        &.format-audio {
			
			.eltdf-post-heading {
				position: relative;
			}

            .eltdf-blog-audio-holder {
				position: absolute;
				width: 100%;
				bottom: 0;

                .mejs-container, .mejs-embed, .mejs-embed body, .mejs-container .mejs-controls {
                    background: none;
                }

                .mejs-container {
                    background-color: $default-heading-color;
                }

                .mejs-container,
                .mejs-container .mejs-controls {
                    height: 38px !important;
                }

                .mejs-container .mejs-controls {
                    padding: 0 10px 0 4px;
                    box-sizing: border-box;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button {
                    width: 39px;
                }

                .mejs-container .mejs-controls > .mejs-volume-button {
                    width: 30px;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button,
                .mejs-container .mejs-controls > .mejs-volume-button {
                    height: 38px;

                    button {
                        position: relative;
                        height: 100%;
                        background: none;
                        background-position: center;
                        background-repeat: no-repeat;
                        margin: 0;
                        border: 0;
                        outline: none;
                    }

                    &.mejs-play button {
                        width: 39px;
                        background-image: url('../img/audio/play.png');
                    }

                    &.mejs-pause button {
                        width: 39px;
                        background-image: url('../img/audio/pause.png');
                    }

                    &.mejs-stop button {
                        width: 39px;
                        background-image: url('../img/audio/stop.png');
                    }

                    &.mejs-mute button {
                        width: 30px;
                        background-image: url('../img/audio/sound.png');
                    }

                    &.mejs-unmute button {
                        width: 30px;
                        background-image: url('../img/audio/mute.png');
                    }
                }

                .mejs-container .mejs-controls > .mejs-time {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;
                    font-size: 13px;
                    color: #fff;
                    line-height: 36px;
                }

                .mejs-container .mejs-controls > .mejs-time-rail {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;

                    .mejs-time-total {
                        height: 5px;
                        margin: 15px 14px;
                        background: none;
                        background-color: #fff;

                        .mejs-time-current {
                            height: 5px;
                            background-color: $first-main-color;
                        }

                        .mejs-time-float {
                            top: -14px;
                            border: 0;
                            color: #fff;

                            .mejs-time-float-corner {
                                border-top-color: $default-heading-color;
                            }
                        }

                        .mejs-time-loaded,
                        .mejs-time-float {
                            background: none;
                        }
                    }
                }

                .mejs-container .mejs-controls > a.mejs-horizontal-volume-slider {
                    height: 38px;

                    .mejs-horizontal-volume-total,
                    .mejs-horizontal-volume-current {
                        height: 5px;
                        top: 15px;
                        background: none;
                    }

                    .mejs-horizontal-volume-total {
                        background-color: #fff;
                    }

                    .mejs-horizontal-volume-current {
                        background-color: $first-main-color;
                    }
                }
            }
        }

        &.format-video {
            .eltdf-self-hosted-video {
                max-width: 100%;
            }
        }
    }
}

/* ==========================================================================
   Blog Common style - end
   ========================================================================== */