/* ==========================================================================
   Blog Single Standard style - begin
   ========================================================================== */

.eltdf-blog-holder.eltdf-blog-single {

	&.eltdf-blog-single-standard {

		article {
			padding: 0 0 6px;
			margin: 0 0 36px;

			.eltdf-post-heading {
				margin: 0 0 32px;
			}

			.eltdf-post-title {
				padding: 0 0 23px;
				margin: 0 0 9px;
			}

			.eltdf-post-info-top {
				margin: 0 0 8px;

				> div {
					position: relative;
					display: inline-block;
					padding: 0 8px 0 0;
					margin: 0 3px 0 0;
					color: $default-text-color;

					&.eltdf-blog-like {

						i {
							margin: 0 3px 0 0;
						}
					}

					a {
						color: inherit;

						&:hover {
							color: $first-main-color;
						}
					}

					&:after {
						content: "/";
						position: absolute;
						top: 0;
						right: -2px;
					}

					&:last-child {
						padding: 0;
						margin: 0;

						&:after {
							content: '';
						}
					}
				}
			}

			.eltdf-post-info-bottom {
				margin: 32px 0 0;

				.eltdf-blog-share {
					display: block;
					border-top: 1px solid $default-border-color;
					padding: 10px 0 0;
					margin: 20px 0 0;
				}
			}

			&.format-link {
				background-color: transparent;

				.eltdf-post-text {
					padding: 0;
				}

				.eltdf-post-link-holder {
					text-align: center;
					color: #fff;
					background-color: $first-main-color;
					padding: 70px;
					margin: 0 0 33px;

					.eltdf-post-title {
						padding: 0;
						margin: 0;
					}
				}

				.eltdf-post-info-bottom {

					a {
						color: #b2b2b2;

						&:hover {
							color: $first-main-color;
						}
					}
				}
			}

			&.format-quote {
				background-color: transparent;

				.eltdf-post-text {
					padding: 0;
				}

				.eltdf-post-quote-holder {
					text-align: center;
					background-color: #fafafa;
					padding: 60px;
					margin: 0 0 32px;
				}

				.eltdf-post-text-main {

					.eltdf-post-title {
						position: relative;
						text-align: left;


						&:before {
							content: " ";
							position: absolute;
							display: block;
							width: 93px;
							bottom: 0;
							left: 0;
							border-top: 1px solid $first-main-color;
						}
					}
				}
			}

			&.format-audio {

				.eltdf-post-heading {
					position: relative;
				}

				.eltdf-blog-audio-holder {

					.mejs-audio {

						.me-cannotplay {

							span {
								padding: 5px 15px;
							}
						}
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Blog Single Standard style - end
   ========================================================================== */