/* ==========================================================================
   Author Info widget style - begin
   ========================================================================== */

.eltdf-author-info-widget {
	@include eltdfRelativeHolderLayout();
	border: 1px solid $default-border-color;
	box-sizing: border-box;
	
    .eltdf-aiw-inner {
        @include eltdfRelativeHolderLayout();
	    padding: 41px 30px 34px;
		text-align: center;
		box-sizing: border-box;
    }
	
	.eltdf-aiw-image {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		border-radius: 100%;
		overflow: hidden;
		
		img {
			display: block;
		}
	}

	.eltdf-aiw-title {
		text-transform: uppercase;
		text-align: center;

		span {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			padding: 0 0 17px;

			&:after {
				content: " ";
				position: absolute;
				display: block;
				width: 80%;
				left: 50%;
				bottom: 0;
				border-top: 1px solid $first-main-color;
				@include eltdfTransform(translateX(-50%));
			}
		}
	}
	
	.eltdf-aiw-text {
		display: block;
		margin: 22px 0 0;
	}
}
/* ==========================================================================
   Author Info widget style - end
   ========================================================================== */