/* ==========================================================================
   Footer style - begin
   ========================================================================== */
/* ==========================================================================
   #Content bottom styles - start
   ========================================================================== */
.eltdf-content-bottom {

    .eltdf-content-bottom-inner{
        box-sizing: border-box;
        
        &.eltdf-grid{
            padding: 20px 0;
        }
        
        &.eltdf-full-width{
            padding: 20px 50px;
        }
    }
}

.eltdf-page-footer {
    @include eltdfRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .eltdf-footer-top-holder {
        @include eltdfRelativeHolderLayout();
		font-size: 14px;
        background-color: #070707;
        box-sizing: border-box;

        .eltdf-footer-top-alignment-center{
            text-align: center;
        }
	    
        .eltdf-footer-top-alignment-left{
            text-align: left;
        }
	    
        .eltdf-footer-top-alignment-right{
            text-align: right;
        }

        .eltdf-footer-top-inner{
            box-sizing: border-box;
	        
            &.eltdf-grid{
                padding: 83px 0;
            }
	        
            &.eltdf-full-width{
				padding: 67px 110px 64px;
            }

			.eltdf-column-content {

				&:after {
					content: " ";
					position: absolute;
					display: block;
					right: 0;
					bottom: 0;
					height: 95%;
					border-right: 1px solid #393939;
				}

				&:last-child:after {
					display: none;
				}
			}
        }

		p {
			font-size: 14px;
		}
    }

    .eltdf-footer-bottom-holder {
        @include eltdfRelativeHolderLayout();
        background-color: #111;
        box-sizing: border-box;
        text-align: center;

        .eltdf-footer-bottom-inner {
            box-sizing: border-box;
			text-align: center;
	        
            &.eltdf-grid {
                padding: 24px 0;
            }
	        
            &.eltdf-full-width {
				padding: 14px 50px
            }
        }
    }
}
/* ==========================================================================
   Footer style - end
   ========================================================================== */