/* ==========================================================================
   Icon widget style - begin
   ========================================================================== */

.eltdf-icon-widget-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
	color: #fff;
	@include eltdfTransition(none);

	&.eltdf-text-bellow,
	&.eltdf-text-above {

		.eltdf-icon-text-holder {
			display: block;
		}
	}

	&.eltdf-text-left {

		.eltdf-icon-text-holder {
			float: left;

			.eltdf-icon-text {
				margin: 0 5px 0;
			}
		}
	}

	&.eltdf-text-right {

		.eltdf-icon-holder {
			margin: 0 15px 0 0;
		}
	}

	.eltdf-circle {
		border: 1px solid #fff;
		border-radius: 50%;
		width: 32px;
		height: 32px;
		line-height: 32px;
		@include eltdfTransition(background-color .2s ease-in-out, border-color .2s ease-in-out, color .2s ease-in-out);
	}

    > * {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        font-size: inherit;
        font-style: inherit;
        font-weight: inherit;
        line-height: inherit;
        letter-spacing: inherit;
        color: inherit;
    }

	.eltdf-icon-holder,
	.eltdf-icon-text-holder {
		@include eltdfTransition(background-color .15s ease-in-out, color .15s ease-in-out);
	}

    .eltdf-top-bar &,
    footer .eltdf-footer-top & {
        color: #fff;

        &:hover {
            color: $first-main-color;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-icon-widget-holder {
    color: $header-dark-color !important;

    &:hover {
        color: $header-dark-hover-color !important;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-icon-widget-holder {
    color: $header-light-color !important;

    &:hover {
        color: $header-light-hover-color !important;
    }
}
/* ==========================================================================
   Icon widget style - end
   ========================================================================== */