/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.eltdf-blog-holder {
	
    &.eltdf-blog-masonry {

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.eltdf-post-has-media {
                .eltdf-post-heading {
                    margin-bottom: 30px;
                }
            }

            .eltdf-post-title {
                margin: 0 0 20px;
            }

            .eltdf-more-link-container {
                @include eltdfRelativeHolderLayout();
                margin: 20px 0 0;
            }
        }
    }
}
/* ==========================================================================
   Blog List - Masonry style - end
   ========================================================================== */